@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Catamaran";
  font-display: swap;

  src: local("Catamaran"), local("Catamaran-Regular"), url("../fonts/Catamaran.woff2") format("woff2"), url("../fonts/Catamaran.woff") format("woff");
}

@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Catamaran";
  font-display: swap;

  src: local("Catamaran Bold"), local("Catamaran-Bold"), url("../fonts/Catamaran_Bold.woff2") format("woff2"), url("../fonts/Catamaran_Bold.woff") format("woff");
}

