@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Catamaran";
  font-display: swap;
  src: local("Catamaran"), local("Catamaran-Regular"), url("../fonts/Catamaran.woff2") format("woff2"), url("../fonts/Catamaran.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Catamaran";
  font-display: swap;
  src: local("Catamaran Bold"), local("Catamaran-Bold"), url("../fonts/Catamaran_Bold.woff2") format("woff2"), url("../fonts/Catamaran_Bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Muli";
  font-display: swap;
  src: local("Muli Light"), local("Muli-Light"), url("../fonts/Muli_Light.woff2") format("woff2"), url("../fonts/Muli_Light.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Muli";
  font-display: swap;
  src: local("Muli Regular"), local("Muli-Regular"), url("../fonts/Muli_Regular.woff2") format("woff2"), url("../fonts/Muli_Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Muli";
  font-display: swap;
  src: local("Muli SemiBold"), local("Muli-SemiBold"), url("../fonts/Muli_Semi-Bold.woff2") format("woff2"), url("../fonts/Muli_Semi-Bold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Muli";
  font-display: swap;
  src: local("Muli Bold"), local("Muli-Bold"), url("../fonts/Muli_Bold.woff2") format("woff2"), url("../fonts/Muli_Bold.woff") format("woff");
}
@font-face {
  font-family: 'Oswald';
  src: local('Oswald Regular'), local('Oswald-Regular'), url('../fonts/Oswaldregular.woff2') format('woff2'), url('../fonts/Oswaldregular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: 'Oswald';
  src: local('Oswald DemiBold'), local('Oswald-DemiBold'), url('../fonts/Oswalddemibold.woff2') format('woff2'), url('../fonts/Oswalddemibold.woff') format('woff');
  font-weight: 600;
  font-style: normal;
}
img:not([alt]),
label:not([for]),
input[type=submit]:not([value]) {
  outline: 2px solid red;
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0, 0, 0, 0);
}
img {
  max-width: 100%;
}
a {
  text-decoration: none;
}
body {
  max-width: 1366px;
  margin: 0 auto;
}
.page-header {
  background: url(../img/mountain.jpg) no-repeat;
  min-height: 650px;
  color: #FCFCFC;
  font-family: Roboto;
  font-style: normal;
  font-size: 16px;
  line-height: 19px;
}
.page-header__logo {
  margin-top: 43px;
}
.page-header__name {
  font-family: Oswald, Arial, Helvetica, sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 98px;
  line-height: 145px;
}
.main-nav {
  display: flex;
  max-width: 1170px;
  margin: 0 auto;
}
.main-nav__list {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
  margin-left: 100px;
  margin-top: 43px;
}
.main-nav__link {
  text-decoration: none;
  color: inherit;
}
.main-nav__link:hover {
  text-decoration: underline;
}
.main-nav__item {
  margin-right: 30px;
}
.main-nav__contacts {
  margin-left: 265px;
}
.contacts {
  margin-left: auto;
  margin-top: 35px;
}
.contacts__phone {
  color: inherit;
}
.contacts__item {
  display: inline-block;
  width: 35px;
  height: 35px;
  border-radius: 50%;
  background-color: rgba(252, 252, 252, 0.4);
  background-repeat: no-repeat;
  background-position: center;
}
.contacts__item:hover {
  filter: invert(100%);
}
.contacts__item--search {
  background-image: url(../img/lens.svg);
  margin-right: 27px;
}
.contacts__item--user {
  background-image: url(../img/men.svg);
}
